import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {
    Jumbotron,
    h1,
    Grid,
    Label,
    Well,
    Row,
    Navbar,
    NavItem,
    Nav,
    Checkbox,
    ProgressBar,
    Col
} from 'react-bootstrap';

import {Words, UniqueWordCount, TotalWordCount}  from './data/words.js'
import Sentences from './data/sentences.js'

class App extends Component {
  constructor() {

        super();
        this.state = {
            words : Words,
            uniqueWordCount : UniqueWordCount,
            totalWordCount : TotalWordCount,
            sentences : Sentences,
            hideDone: true,
            learnt: 0
        }
  }

  render() {

    const sentences = this.state.sentences;
    const maxExamples = 3;
    const uniqueWordCount = this.state.uniqueWordCount;
    const totalWordCount = this.state.totalWordCount;
    let accumNormalizedFreq = 0;
    let hideDone = this.state.hideDone;
    let handleDone = this.handleDone;

    return (
      <div className="App">
        <Navbar fixedTop inverse>
          <Grid> 
            <Row>
              <Col xs={8} textCenter>
               <h3><Label bsStyle="danger">Proficiency level: {this.state.learnt}% </Label></h3>
              </Col>
            </Row>
          </Grid>
        </Navbar>

        <Grid >
          <h1>-------------------</h1>

          {this.state.words.map(function(wordObject, index){
            let nExamples = wordObject.sentences.length >= maxExamples? maxExamples :  wordObject.sentences.length;
            let examples = [];
            let normalizedFreq = wordObject.frequency / totalWordCount;
            let percentage = index / uniqueWordCount;
            accumNormalizedFreq += normalizedFreq;

            for (let i = 0; i<nExamples; i++){
                    examples.push(sentences[wordObject.sentences[i]].text);
                  }

            return <Well>
                        <Row> 
                          <Col xs={4}>
                            <h3>
                              {index+1}
                            </h3>
                            <h4>
                              <Label bsStyle="warning">{Math.round(percentage* 10000)/100}%</Label>
                              
                            </h4>
                            <h4>
                              <Label bsStyle="success">{Math.round(normalizedFreq * 10000)/100}% </Label>
                            </h4>
                            <h4>
                              <Label bsStyle="danger">{Math.round(accumNormalizedFreq* 10000)/100}% </Label>  
                            </h4>
                          </Col>
                          <Col xs={8}><h2>{wordObject._id}</h2></Col>
                          <Col xs={8}>
                            {examples.map(function(example){
                              return <p>{example}</p>
                            })}
                          </Col>
                          <hr/> 
                        </Row> 
                        <Row> 
                          <Checkbox value={wordObject.isDone} onChange={() => handleDone(normalizedFreq)}>
                            Done
                          </Checkbox>
                        </Row>  
                      </Well>
            
          })}
          

        </Grid>
        
      </div>
    );
  }


  componentDidMount() {
    let words = this.state.words;
    words.map(function(word){
      word.isDone = false;
    });
  }

  handleDone = (normalizedFreq) => {
    let learnt = this.state.learnt;
    learnt += normalizedFreq * 100;
    learnt = Math.round(learnt* 100)/100;
    this.setState({learnt});
  }


}

export default App;


